.industry .inner {
	margin-bottom: 1rem
}

.industry .primary-title {
	border-bottom: #E5E9F2 1px solid;
}

.industry .primary-title h2 {
	display: inline-block;
	font-size: 1.125rem;
	padding-left: .5rem;
	vertical-align: middle
}

.industry .secondary-title {
	margin: .5rem 0 1rem 1rem;
}

.industry .secondary-title:after {
	content: '';
	position: absolute;
	left: auto;
	top: 50%;
	right: 0;
	width: 100%;
	height: 0;
	border-top-style: dashed;
	border-top-width: 1px;
	border-color: #C6C6C6;
}

.industry .secondary-title h3 {
	display: inline-block;
	margin-bottom: 0;
	padding-right: 1rem;
	background-color: white;
	z-index: 2;
	font-size: .875rem;
}

.industry-item {
	display: inline-block;
	padding: 0 .75rem;
	vertical-align: middle;
	width: 50%;
}

.industry-item .checkbox-input {
	display: none;
}

.industry-item .checkbox-text,
.industry-item .checkbox-text--description {
	transition: all .4s ease;
}

.industry-item .checkbox-label {
	display: inline-block;
	width: 100%;
	padding: 1.125rem 1.25rem;
	cursor: pointer;
	margin: 1rem 0 .5rem;
	background-color: #FAFAFA;
	border: 1px solid #EDEEEF;
	border-radius: 2px;
	box-shadow: inset 0 0 0 0 #0E86D4;
	text-align: center;
	transition: all .25s ease;
	user-select: none
}

.industry-item .checkbox-label:before {
	content: '';
	position: absolute;
	top: 25%;
	left: .5rem;
	width: 1.875rem;
	height: 1.875rem;
	background: #fff;
	border: 1px solid #E8EAEB;
	border-radius: 50%;
	transform: translate(0%, -50%);
	transition: all .4s ease;
}

.industry-item .checkbox-text--title {
	color: #A7B1B6;
	font-weight: bolder;
}

.industry-item .checkbox-text--description {
	border-top: 1px solid #dfe6e9;
	color: #A7B1B6;
	font-size: .875rem;
	margin-top: .5rem;
	padding-top: .5rem;
}

.industry-item .checkbox-text--description .un {
	display: none;
}

.industry-item .checkbox-input:checked + .checkbox-label {
	background-color: #fff;
	border-color: #0E86D4;
	box-shadow: inset 0 -12px 0 0 #0E86D4;
}

.industry-item .checkbox-input:checked + .checkbox-label:before {
	background-color: #0E86D4;
	background-image: url('../img/check.svg');
	background-position: center;
	background-repeat: no-repeat;
	background-size: 1.5rem;
	box-shadow: 0 1px 2px rgba(0, 0, 0, 0.16);
	top: 0;
}

.industry-item .checkbox-input:checked + .checkbox-label .checkbox-text {
	-webkit-transform: translate(0, -8px);
	transform: translate(0, -8px);
}

.industry-item .checkbox-input:checked + .checkbox-label .checkbox-text--title {
	color: #3c4856;
	font-size: 1.125rem;
}

.industry-item .checkbox-input:checked + .checkbox-label .checkbox-text--description {
	border-color: #0E86D4;
}

.industry-item .checkbox-input:checked + .checkbox-label .checkbox-text--description .un {
	display: inline-block;
}

.industry-another {
	padding: 0 1rem .5rem;
	width: 100%;
}

.industry-another input {
	box-shadow: 0px 0px 4px #CDD8E3;
	padding-top: .75rem;
	padding-bottom: .75rem;
	padding-left: 3rem;
	width: 100%;
}

.industry-another-icon {
	position: absolute;
	left: 1rem
}

.industry-another-icon:after {
	content: '';
	background: url('../img/duty.svg') no-repeat center center;
	background-size: contain;
	opacity: .4;
	position: absolute;
	height: 1.25rem;
	width: 1.25rem;
	left: .75rem;
	top: .75rem;
}

.industry-another input:focus+.industry-another-icon:after {
	opacity: 1;
	transition: .5s;
}

.blockade_corp-no {
	color: #A7B1B6;
	display: inline-block;
	font-size: 1rem;
}

.blockade_corp-name > .txt {
	max-width: 170px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	word-wrap: normal;
	font-size: 1rem;
	display: block;
}

.blockade_corp-job_vacancies .btn {
	width: 100%;
}

.blockade_corp-controller {
	width: 7.5rem;
}

.ctrl-del .btn {
	width: 2.25rem;
	height: 2.25rem;
	padding: 0;
}

.blockade_corp-controller .btn {
	width: 36px;
	height: 36px;
	padding: 0;
	border-radius: 4px;
}

.blockade_corp-bd .btn [class^="icon"] {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
	font-size: 1.6rem;
}