* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    position: relative;
}

html {
    font-size: 16px;
    scroll-behavior: smooth
}

@media all and (max-width:992px) {
    html {
        font-size: 15px
    }

}

@media all and (max-width:768px) {
    html {
        font-size: 14px
    }

}

@media all and (max-width:480px) {
    html {
        font-size: 13px
    }

}

body {
    background-color: #dde3e8;
    color: #233138;
    font-family: 'Rubik', -apple-system, 'Helvetica Neue', Helvetica, Arial, 'Microsoft JhengHei', 微軟正黑體, '微軟正黑體', 'PingFang SC', 'Noto Sans CJK TC', sans-serif;
    line-height: 1.5;
    margin: 0;
    min-height: 100%;
    text-rendering: optimizeSpeed;
}

hr {
    border-top: 0;
    border-right: 0;
    border-left: 0;
    border-bottom: 1px dashed #e5e5e5;
    height: 1px;
    overflow: visible;
}

pre,
code,
kbd,
samp {
    font-family: monospace, monospace;
    font-size: 1rem;
    margin: .5rem 0
}

small {
    font-size: 75%;
}

big {
    font-size: 125%;
}

sub,
sup {
    font-size: 75%;
    line-height: 0;
    vertical-align: baseline;
}

sub {
    bottom: -.25rem;
}

sup {
    top: -.5rem;
}

a {
    color: #1984c9;
    text-decoration: none;
    transition: all .125s ease-in-out;
}

a:hover {
    color: #84caf7;
}

a[href^="http"]:empty:before {
    content: attr(href);
}

figure,
h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}

b,
strong,
h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: bolder;
}

dl,
ol,
ul {
    margin: 0;
    padding: 0;
    list-style: none
}

dd {
    margin-inline-start: 0;
}

table {
    width: 100%;
    background-color: transparent;
    border-collapse: collapse;
}

button,
input {
    overflow: visible;
}

button,
select {
    text-transform: none;
}

button,
input,
optgroup,
select,
textarea {
    font-family: 'Rubik', -apple-system, 'Helvetica Neue', Helvetica, Arial, 'Microsoft JhengHei', 微軟正黑體, '微軟正黑體', 'PingFang SC', 'Noto Sans CJK TC', sans-serif;
    font-size: 100%;
    line-height: 1.5;
    margin: 0;
    outline: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
    -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
    border-style: none;
    padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
    outline: 1px dotted ButtonText;
}

fieldset {
    padding: 0.35em 0.75em 0.625em;
}

legend {
    color: inherit;
    display: table;
    max-width: 100%;
    padding: 0;
    white-space: normal;
}

progress {
    vertical-align: baseline;
}

textarea {
    overflow: auto;
}

[type="checkbox"],
[type="radio"] {
    padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
    height: auto;
}

[type="search"] {
    -webkit-appearance: textfield;
    outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

::-webkit-file-upload-button {
    -webkit-appearance: button;
    font: inherit;
}

details {
    display: block;
}

summary {
    display: list-item;
}

template {
    display: none;
}

[hidden] {
    display: none;
}

img,
svg,
image {
    border-style: none;
    height: auto;
    max-width: 100%;
    vertical-align: middle;
}

h1 {
    font-size: 2.5rem
}

h2 {
    font-size: 2.25rem
}

h3 {
    font-size: 2rem
}

h4 {
    font-size: 1.75rem
}

h5 {
    font-size: 1.5rem
}

h6 {
    font-size: 1.125rem
}

p {
    font-size: 1rem
}

span {
    font-size: .875rem
}

label {
    font-size: .9375rem
}

em {
    font-style: italic
}
