/* WebKit browsers */
::-webkit-input-placeholder {
    color: #a7b1b6;
}

/* Mozilla Firefox 4 to 18 */
:-moz-placeholder {
    color: #a7b1b6;
}

/* Mozilla Firefox 19+ */
::-moz-placeholder {
    color: #a7b1b6;
    opacity: 1
}

/* Internet Explorer 10+ */
:-ms-input-placeholder {
    color: #a7b1b6 !important;
}

.form-group {
    margin-bottom: 1.25rem;
}

.form-control {
    display: block;
    width: 100%;
}

.form-control.is-valid,
.was-validated .form-control:valid {
    padding-right: 2.75rem;
    border-color: #1cc65e;
}

.form-control.is-invalid,
.was-validated .form-control:invalid {
    padding-right: 2.75rem;
    border-color: #f96e5b;
}

.valid-tooltip {
    background-color: #5ed84f;
}

.invalid-tooltip,
.valid-tooltip {
    position: absolute;
    line-height: 1.45;
    max-width: 100%;
    border-radius: .25rem;
    top: 0rem;
    right: 0rem;
    font-size: .75rem;
    display: none;
    margin-top: .1rem;
    padding: .25rem .5rem;
    color: #ffffff;
}

.invalid-tooltip {
    background-color: #f96e5b;
}

.was-validated .form-control:valid~.valid-tooltip {
    display: inline-block;
}

.was-validated .form-control:invalid~.invalid-tooltip {
    display: inline-block;
}

.form-control[disabled],
fieldset[disabled] .form-control {
    cursor: not-allowed;
}

.form-control[disabled],
.form-control[readonly],
fieldset[disabled] .form-control {
    background-color: #eff3f5;
    opacity: 1;
}

.form-check {
    margin-bottom: 1.25rem;
}

.complate-list {
    margin-bottom: .5rem;
}

input[type=email],
input[type=url],
input[type=search],
input[type=tel],
input[type=color],
input[type=text],
input[type=password],
input[type=datetime],
input[type=datetime-local],
input[type=date],
input[type=month],
input[type=time],
input[type=week],
input[type=number],
textarea {
    background-color: #ffffff;
    border: 1px #cdd8e3 solid;
    border-radius: 2px;
    box-shadow:0 .125rem .25rem rgba(0, 0, 0, .04);
    font-size: .875rem;
    padding: .625rem .875rem;
    transition: all .125s ease-in-out;
}

input[type=email]:focus,
input[type=url]:focus,
input[type=search]:focus,
input[type=tel]:focus,
input[type=color]:focus,
input[type=text]:focus,
input[type=password]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=date]:focus,
input[type=month]:focus,
input[type=time]:focus,
input[type=week]:focus,
input[type=number]:focus,
textarea:focus {
    border-color: #68bbe3;
    box-shadow: 0 0 8px 0 #68bbe3
}

.input--xs.input--xs {
    font-size: .75rem;
    padding: .375rem .625rem;
    max-width: 12rem;
}

.input--sm.input--sm {
    padding: .5rem .75rem;
    width: 16rem;
    max-width: 100%;
}

.input--md.input--md {
    width: 20rem;
    max-width: 100%;
}

.input--lg.input--lg {
    padding: .875rem 1rem;
    width: 24rem;
    max-width: 100%;
}

.input--xlg.input--xlg {
    padding: 1rem 1.125rem;
    width: 28rem;
    max-width: 100%;
}

.input--xxlg.input--xxlg {
    padding: 1.125rem 1.25rem;
    width: 32rem;
    max-width: 100%;
}

.input--full.input--full {
    width: 100%
}

.input--bt-line-group {
    overflow: hidden;
}

.input--bt-line.input--bt-line {
    border: transparent 1px solid;
    box-shadow: none;
    background-color: #f5f7f8;
    caret-color: #0e86d4;
    width: 100%;
}

.input--bt-line.input--bt-line:focus {
    background-color: #f8fafb;
    border: transparent 1px solid;
}

.input--bt-line.input--bt-line ~ .bt-line {
    position: absolute;
    left: 0;
    bottom: 0;
    transform: translateX(-50%);
    width: 0;
    height: 2px;
    background-color: #0e86d4;
    transition: .25s all linear;
}

.input--bt-line.input--bt-line:focus ~ .bt-line {
    width: 100%;
    transform: translateX(0);
}

/* WebKit browsers */
.input--bt-line.input--bt-line::-webkit-input-placeholder {
    color: #8fa2ad;
    letter-spacing: 1px;
}

/* Mozilla Firefox 4 to 18 */
.input--bt-line.input--bt-line:-moz-placeholder {
    color: #8fa2ad;
    letter-spacing: 1px;
}

/* Mozilla Firefox 19+ */
.input--bt-line.input--bt-line::-moz-placeholder {
    color: #8fa2ad;
    letter-spacing: 1px;
    opacity: 1
}

/* Internet Explorer 10+ */
.input--bt-line:-ms-input-placeholder {
    color: #8fa2ad;
    letter-spacing: 1px;
}

.input--bt-line[disabled],
fieldset[disabled] .input--bt-line {
    cursor: not-allowed;
}

.input--bt-line[disabled],
.input--bt-line[readonly],
fieldset[disabled] .input--bt-line {
    background-color: #e9eef1;
    opacity: 1;
}

