@media all and (max-width:992px) {
    .toggle-menu {
        background-color: #68BBE3;
        border: 1px solid #aed0e4;
        border-radius: 2px;
        padding: 1rem 1.25rem;
        position: absolute;
        left: 1rem;
        top: 50%;
        transform: translateY(-50%);
        z-index: 1;
    }

    .toggle-menu span {
        content: '';
        background-color: #fff;
        height: 2px;
        position: absolute;
        left: calc(50% - 13px);
        top: calc(50% - 1px);
        transform-origin: 50% 50%;
        transition: background-color .2s ease-in-out, top .2s .2s ease-out, transform .2s linear, -webkit-transform .2s linear;
        width: 26px;
    }

    .toggle-menu span:before,
    .toggle-menu span:after {
        content: '';
        background-color: #fff;
        height: 2px;
        position: absolute;
        width: 26px;
    }

    .toggle-menu span:before {
        top: 7px;
    }

    .toggle-menu span:after {
        top: -7px;
    }

    .toggle-menu.active span {
        background-color: transparent;
        transition: background .2s ease-out;
    }

    .toggle-menu.active span:before,
    .toggle-menu.active span:after {
        transition: top .2s ease-out, transform .2s .2s ease-out, -webkit-transform .2s .2s ease-out;
    }

    .toggle-menu.active span:before {
        top: 0;
        transform: rotate3d(0, 0, 1, -45deg);
    }

    .toggle-menu.active span:after {
        top: 0;
        transform: rotate3d(0, 0, 1, 45deg);
    }

}

/*--    Headers    ===================*/
#header {
    z-index: 99
}

#header .header {
    background-color: #fff;
    box-shadow: 0 .5rem 2.5rem rgba(35, 49, 56,.025),
     0 .9375rem 1.375rem rgba(35, 49, 56,.0375),
     0 .25rem .5rem rgba(35, 49, 56,0.05),
     0 .125rem .1875rem rgba(35, 49, 56,.0625);
    height: 3.75rem;
}

.header-left {
    float: left;
    height: 3.75rem;
    width: 16.625rem;
}

.header-logo {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.header-right {
    display: inline-block;
    font-size: 0;
    text-align: center;
    width: calc(100% - 16.625rem);
}

.header-right a {
    font-size: .875rem;
}

@media all and (max-width:992px) {
    .header-left {
        width: 100%
    }

    .header-right {
        display: none
    }

}

/*--    Header Search    ===================*/
.searchlist,
.search-bar-box,
.search-bar-box .search-bar {
    display: inline-block;
    vertical-align: middle;
}

.searchlist {
    text-align: center;
    height: 3.75rem;
    width: 5.125rem;
}

#searchLog {
    line-height: 3.75rem;
}

#searchLog>i {
    margin-left: .25rem;
}

#searchLogCnt {
    border-radius: 2px;
    text-align: left;
    position: absolute;
    left: 0;
    top: 3rem;
    width: 22rem;
    z-index: 2;
    box-shadow: 0 4px 18px rgba(0,0,0,0.12);
    background-color: #fff;
    border: 1px solid #e9eef1;
    border-radius: 4px;
    display: none;
}

