#searchLogCnt .search-log-title {
    background-color: #72828A;
    border-radius: 4px 4px 0 0 ;
    color: #fff;
    font-size: .875rem;
    font-weight: bolder;
    padding: .75rem 1.25rem;
    width: 100%;
}

#searchLogCnt .search-log-title .close-btn {
    color: #fff;
    position: absolute;
    right: .75rem;
    top: 50%;
    transform: translateY(-50%);

}
#searchLogCnt .search-log-title .close-btn i{
    font-size: 1rem;
}
#searchLogCnt .search-log-content {
    padding: .75rem 1.25rem;
}
#searchLogCnt .search-log-content ul {
    margin-bottom: .75rem;
}

#searchLogCnt .search-log-content li {
    padding: .25rem 0
}

#searchLogCnt .search-log-content li a {
    text-decoration: none
}

#searchLogCnt .search-log-content li a:not(:last-child):after {
    content: '、';
    color: #233138;
    cursor: auto;
}

#searchLogCnt .search-log-content .type {
    border-left: 3px solid #dce8f1;
    color: #72828a;
    padding-left: .375rem;
    padding-right: 1.5rem;
    font-size: 0.8125rem
}

#searchLogCnt .search-log-content .type:after {
    content: '';
    background-color: #bfc3c7;
    width: .75rem;
    height: 1px;
    position: absolute;
    top: 50%;
    right: .375rem;
}

#searchLogCnt .search-log-footer {
    border-top: 1px dotted #dfe6e9;
    text-align: right;
    padding-top: .75rem;
}

.search-bar-box {
    padding: 0 .5rem;
    width: calc(100% - 5.125rem);
}

.search-bar-box .search-bar {
    background-color: rgba(129, 167, 205, 0.12);
    border: 1px solid rgba(129, 167, 205, 0.06);
    border-radius: 4px;
    padding: .25rem .5rem;
    height: 2.875rem;
    width: 100%;
}

.search-bar i {
    position: absolute;
    left: .5rem;
    top: .5rem;
    color: #0E86D4;
    font-size: 1.5rem;
    opacity: .4;
    transition: .5s;
}

.search-bar input {
    width: 100%;
    height: 38px;
    line-height: 0
}

.search-bar input[type="text"] {
    background: transparent;
    border: none;
    box-shadow: none;
    font-size: .8125rem;
    padding: 0 0 0 40px;
}

.search-bar input[type="text"]:focus+.search-icon {
    opacity: 1;
}

.search-bar .right-border {
    background: rgba(129, 167, 205, 0.06);
    height: 100%;
    width: 1px;
    position: absolute;
    top: 0;
    right: 0;
}

@media all and (max-width:992px) {
    .search-bar input[type="text"] {
        padding: 0 0 0 28px;
    }

}

@media all and (max-width:768px) {
    .searchlist,
    .search-bar-box .search-bar {
        display: none
    }

}