$bd_light_1: 	#eff3f5	!default;
$bd_light_2: 	#dfe6e9	!default;
$bd_light_3: 	#e9eef1	!default;
$btn_lightgrey: #C4CDD5 !default;
$btn_light: 	#E9EEF1 !default;



// Color system
$white:   		#fff	!default;
$gray-100: 		#f8f9fa !default;
$gray-200: 		#e9ecef !default;
$gray-300: 		#dee2e6 !default;
$gray-400: 		#ced4da !default;
$gray-500: 		#a7b1b6 !default;
$gray-600: 		#6c757d !default;
$gray-700: 		#72828a !default;
$gray-800: 		#343a40 !default;
$gray-900: 		#374c57 !default;
$black:    		#233138 !default;

$greys: () !default;
$greys: map-merge(
  	(
	    "100": $gray-100,
	    "200": $gray-200,
	    "300": $gray-300,
	    "400": $gray-400,
	    "500": $gray-500,
	    "600": $gray-600,
	    "700": $gray-700,
	    "800": $gray-800,
	    "900": $gray-900
	),
	$greys
	);

$btn-colors: () !default;
$btn-colors:	map-merge(
	(	
	  	"lightgrey":    	$btn_lightgrey,
	  	"light": 			$btn_light,
	    "new--primary":     $new_primary,
	    "primary":     		$primary,
	    "secondary":     	$secondary,
	    "apply":       		$apply,
	    "positive":        	$positive,
	    "negative":     	$negative,
	    "danger": 			$danger,
		"warning": 			$warning,
		"success": 			$success,
		"cyan": 			$cyan,
		"yellow": 			$yellow,
		"pink": 			$pink,
		"purple": 			$purple,
	    "grey":       		$gray-500,
	    "dark":  			$black,
	),
	$btn-colors
	);


$bg-colors: () !default;
$bg-colors:	map-merge(
	(
	"dark": 			$dark,
	"grey": 			$grey,
	"lightgrey": 		$lightgrey,
	"lightgrey_1": 		$lightgrey_1,
	"biscay": 			$biscay ,
	"forestblue": 		$forestblue,
	"brand-red":		$brand-red ,
	"white": 			$white 	,
	"whitesmoke": 		$whitesmoke ,
	"whiteblue": 		$whiteblue ,
	"default": 			$default ,
	"light_1":			$light_1 ,
	"light_2":			$light_2 ,
	"light_3":			$light_3 ,
	"light_4":	 		$light_4,
	"lightblue":		$lightblue ,
	"smoke": 			$smoke,
	"ltghtsmoke_1":		$ltghtsmoke_1 ,
	"ltghtsmoke_2":		$ltghtsmoke_2 ,
	"smokepurple": 		$smokepurple ,
	"smokeblue_1":		$smokeblue_1 ,
	"smokeblue_2":		$smokeblue_2 ,
	"bg_vanancy": 		$bg_vanancy ,
	"linkblue": 		$linkblue ,
	"linkblueHover": 	$linkblueHover,
	"bd_light_1": 		$bd_light_1	,
	"bd_light_2": 		$bd_light_2	,
	"bd_light_3": 		$bd_light_3	,
	),
	$bg-colors
	);


	// Brands colors
	$facebook:      #3b5999;
	$twitter:       #1da1f2;
	$google-plus:   #dd4b39;
	$instagram:     #e4405f;
	$pinterest:     #bd081c;
	$youtube:       #cd201f;
	$slack:         #3aaf85;
	$dribbble:      #ea4c89;
