	$twitch:        #4B367C;
	$paypal:        #ecb32c;
	$behance:       #0057ff;
	$reddit:        #E84422;
	// Brand colors
	$brand-colors: ()!default;
	$brand-colors: map-merge(
	(
		"facebook":     $facebook,
		"twitter":      $twitter,
		"google-plus":  $google-plus,
		"instagram":    $instagram,
		"pinterest":    $pinterest,
		"youtube":      $youtube,
		"slack":        $slack,
		"dribbble":     $dribbble,
		"dropbox":      $dropbox,
	    "twitch":       $twitch,
		"paypal":       $paypal,
		"behance":      $behance,
		"reddit" :      $reddit,  
		"github":       $github
	), 
	$brand-colors
	);

	$transition-base:             all .25s ease-in-out 	!default;
	$transition-fade:             opacity .15s linear 	!default;
	$transition-collapse:         height .35s ease 		!default;

	// stylelint-disable value-keyword-case
	$font-family-sans-serif:      'Rubik', -apple-system, 'Helvetica Neue', Helvetica, Arial, 'Microsoft JhengHei', 微軟正黑體, '微軟正黑體', 'PingFang SC', 'Noto Sans CJK TC', sans-serif !default;
	$font-family-monospace:       'Rubik', -apple-system, 'Helvetica Neue', Helvetica, Arial, 'Microsoft JhengHei', 微軟正黑體, '微軟正黑體', 'PingFang SC', 'Noto Sans CJK TC', sans-serif !default;
	$font-family-base:            'Rubik', -apple-system, 'Helvetica Neue', Helvetica, Arial, 'Microsoft JhengHei', 微軟正黑體, '微軟正黑體', 'PingFang SC', 'Noto Sans CJK TC', sans-serif !default;
	// stylelint-enable value-keyword-case

	$font-size-base:              1rem !default; // Assumes the browser default, typically `16px`
	$font-size-lg:                $font-size-base * 1.25 !default;
	$font-size-md:                $font-size-base * .1 !default;
	$font-size-sm:                $font-size-base * .875 !default;

	$font-weight-lighter:         lighter !default;
	$font-weight-light:           300 !default;
	$font-weight-normal:          400 !default;
	$font-weight-bold:            700 !default;
	$font-weight-bolder:          bolder !default;

	$font-weight-base:            $font-weight-normal !default;
	$line-height-base:            1.5 !default;

	$h1-font-size:                $font-size-base * 2.5 !default;
	$h2-font-size:                $font-size-base * 2 !default;
	$h3-font-size:                $font-size-base * 1.75 !default;
	$h4-font-size:                $font-size-base * 1.5 !default;
	$h5-font-size:                $font-size-base * 1.25 !default;
	$h6-font-size:                $font-size-base !default;


// Extra small screen / phone
//** Deprecated `$screen-xs` as of v3.0.1
$screen-xs:                  480px !default;
//** Deprecated `$screen-xs-min` as of v3.2.0
$screen-xs-min:              $screen-xs !default;
//** Deprecated `$screen-phone` as of v3.0.1
$screen-phone:               $screen-xs-min !default;

// Small screen / tablet
//** Deprecated `$screen-sm` as of v3.0.1
$screen-sm:                  768px !default;
$screen-sm-min:              $screen-sm !default;
//** Deprecated `$screen-tablet` as of v3.0.1
$screen-tablet:              $screen-sm-min !default;

// Medium screen / desktop
//** Deprecated `$screen-md` as of v3.0.1
$screen-md:                  992px !default;
$screen-md-min:              $screen-md !default;
//** Deprecated `$screen-desktop` as of v3.0.1
$screen-desktop:             $screen-md-min !default;

// Large screen / wide desktop
//** Deprecated `$screen-lg` as of v3.0.1
$screen-lg:                  1200px !default;
$screen-lg-min:              $screen-lg !default;
//** Deprecated `$screen-lg-desktop` as of v3.0.1
$screen-lg-desktop:          $screen-lg-min !default;

// So media queries don't overlap when required, provide a maximum
$screen-xs-max:              ($screen-sm-min - 1) !default;
$screen-sm-max:              ($screen-md-min - 1) !default;
$screen-md-max:              ($screen-lg-min - 1) !default;

//== Grid system
//
//## Define your custom responsive grid.

//** Number of columns in the grid.
$grid-columns:              12 !default;
//** Padding between columns. Gets divided in half for the left and right.
$grid-gutter-width:         16px !default;
// Navbar collapse
//** Point at which the navbar becomes uncollapsed.
$grid-float-breakpoint:     $screen-sm-min !default;
//** Point at which the navbar begins collapsing.
$grid-float-breakpoint-max: ($grid-float-breakpoint - 1) !default;


//== Container sizes
//
//## Define the maximum width of `.container` for different screen sizes.

// Small screen / tablet
$container-tablet:             (720px + $grid-gutter-width) !default;
//** For `$screen-sm-min` and up.
$container-sm:                 $container-tablet !default;

// Medium screen / desktop
$container-desktop:            (940px + $grid-gutter-width) !default;
//** For `$screen-md-min` and up.
$container-md:                 $container-desktop !default;

// Large screen / wide desktop
$container-large-desktop:      (1140px + $grid-gutter-width) !default;
//** For `$screen-lg-min` and up.
$container-lg:                 $container-large-desktop !default;




// Options
//
// Quickly modify global styling by enabling or disabling optional features.
$enable-rounded:                              true !default;
$enable-prefers-reduced-motion-media-query:   true !default;
$enable-transitions:                          true !default;
$enable-shadows:                              true !default;
$enable-gradients:                            false !default;
$enable-pointer-cursor-for-buttons:           true !default;



// Set a specific jump point for requesting color jumps
$theme-color-interval:      8% !default;

// The yiq lightness value that determines when the lightness of color changes from "dark" to "light". Acceptable values are between 0 and 255.
$yiq-contrasted-threshold:  150 !default;

// Customize the light and dark text colors for use in our YIQ color contrast function.
$yiq-text-dark:             $gray-900 !default;
$yiq-text-light:            $white !default;


// Body
//
// Settings for the `<body>` element.

$body-bg:                   $white !default;
$body-color:                $dark !default;


// Links
//
// Style anchor elements.

$link-color:                              theme-color("primary") !default;
$link-decoration:                         none !default;
$link-hover-color:                        darken($link-color, 15%) !default;
$link-hover-decoration:                   underline !default;
// Darken percentage for links with `.text-*` class (e.g. `.text-success`)
$emphasized-link-hover-darken-percentage: 15% !default;


// Components
//
// Define common padding and border radius sizes and more.

$line-height-lg:              1.5 !default;
$line-height-sm:              1.5 !default;

$border-width:                1px !default;
$border-color:                $gray-300 !default;

$border-radius:               .25rem !default;
$border-radius-lg:            .3rem !default;
$border-radius-sm:            .2rem !default;

$rounded-pill:                50rem !default;

$box-shadow-sm:               0 .125rem .25rem rgba($black, .075) !default;
$box-shadow:                  0 .5rem 1rem rgba($black, .15) !default;
$box-shadow-lg:               0 1rem 3rem rgba($black, .175) !default;

$component-active-color:      $white !default;
$component-active-bg:         theme-color("primary") !default;

$caret-width:                 .3em !default;
$caret-vertical-align:        $caret-width * .85 !default;
$caret-spacing:               $caret-width * .85 !default;

$transition-base:             all .2s ease-in-out !default;
$transition-fade:             opacity .15s linear !default;
$transition-collapse:         height .35s ease !default;


// Typography
//
// Font, line-height, and color for body text, headings, and more.

// stylelint-disable value-keyword-case
$font-family-sans-serif:      -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;
$font-family-monospace:       SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;
$font-family-base:            $font-family-sans-serif !default;
// stylelint-enable value-keyword-case

$font-size-base:              1rem !default; // Assumes the browser default, typically `16px`
$font-size-lg:                $font-size-base * 1.25 !default;
$font-size-md:                $font-size-base * .1 !default;
$font-size-sm:                $font-size-base * .875 !default;

$font-weight-lighter:         lighter !default;
$font-weight-light:           300 !default;
$font-weight-normal:          400 !default;
$font-weight-bold:            700 !default;
$font-weight-bolder:          bolder !default;

$font-weight-base:            $font-weight-normal !default;
$line-height-base:            1.5 !default;

$h1-font-size:                $font-size-base * 2.5 !default;
$h2-font-size:                $font-size-base * 2 !default;
$h3-font-size:                $font-size-base * 1.75 !default;
$h4-font-size:                $font-size-base * 1.5 !default;
$h5-font-size:                $font-size-base * 1.25 !default;
$h6-font-size:                $font-size-base !default;



// Buttons + Forms
//
// Shared variables that are reassigned to `$input-` and `$btn-` specific variables.

$input-btn-padding-y:         .375rem !default;
$input-btn-padding-x:         .75rem !default;
$input-btn-font-family:       null !default;
$input-btn-font-size:         $font-size-base * .875 !default;
$input-btn-line-height:       $line-height-base !default;

$input-btn-focus-width:       .125rem !default;
$input-btn-focus-color:       rgba($component-active-bg, .25) !default;
$input-btn-focus-box-shadow:  0 0 0 $input-btn-focus-width $input-btn-focus-color !default;

$input-btn-padding-y-xxs:      .25rem !default;
$input-btn-padding-x-xxs:      .375rem !default;
$input-btn-font-size-xxs:      $font-size-base * .75 !default;
$input-btn-line-height-xxs:    $line-height-sm !default;

$input-btn-padding-y-xs:      .375rem !default;
$input-btn-padding-x-xs:      .75rem !default;
$input-btn-font-size-xs:      $font-size-base * .75 !default;
$input-btn-line-height-xs:    $line-height-sm !default;

$input-btn-padding-y-sm:      .5rem !default;
$input-btn-padding-x-sm:      .875rem !default;
$input-btn-font-size-sm:      $font-size-base * .8125 !default;
$input-btn-line-height-sm:    $line-height-sm !default;


$input-btn-padding-y-md :      .75rem !default;
$input-btn-padding-x-md :      1.5rem !default;
$input-btn-font-size-md :      $font-size-base * .9375 !default;
$input-btn-line-height-md :    $line-height-lg !default;


$input-btn-padding-y-lg:      1rem !default;
$input-btn-padding-x-lg:      3rem !default;
$input-btn-font-size-lg:      $font-size-base !default;
$input-btn-line-height-lg:    $line-height-lg !default;

$input-btn-border-width:      $border-width !default;


// Buttons
//
// For each of Bootstrap's buttons, define text, background, and border color.

$btn-padding-y:               $input-btn-padding-y !default;
$btn-padding-x:               $input-btn-padding-x !default;
$btn-font-family:             $input-btn-font-family !default;
$btn-font-size:               $input-btn-font-size !default;
$btn-line-height:             $input-btn-line-height !default;

$btn-padding-y-xxs:            $input-btn-padding-y-xxs !default;
$btn-padding-x-xxs:            $input-btn-padding-x-xxs !default;
$btn-font-size-xxs:            $input-btn-font-size-xxs !default;
$btn-line-height-xxs:          $input-btn-line-height-xxs !default;


$btn-padding-y-xs:            $input-btn-padding-y-xs !default;
$btn-padding-x-xs:            $input-btn-padding-x-xs !default;
$btn-font-size-xs:            $input-btn-font-size-xs !default;
$btn-line-height-xs:          $input-btn-line-height-xs !default;

$btn-padding-y-sm:            $input-btn-padding-y-sm !default;
$btn-padding-x-sm:            $input-btn-padding-x-sm !default;
$btn-font-size-sm:            $input-btn-font-size-sm !default;
$btn-line-height-sm:          $input-btn-line-height-sm !default;




$btn-padding-y-md:            $input-btn-padding-y-md !default;
$btn-padding-x-md:            $input-btn-padding-x-md !default;
$btn-font-size-md:            $input-btn-font-size-md !default;
$btn-line-height-md:          $input-btn-line-height-md !default;

$btn-padding-y-lg:            $input-btn-padding-y-lg !default;
$btn-padding-x-lg:            $input-btn-padding-x-lg !default;
$btn-font-size-lg:            $input-btn-font-size-lg !default;
$btn-line-height-lg:          $input-btn-line-height-lg !default;

$btn-border-width:            $input-btn-border-width !default;

$btn-font-weight:             $font-weight-normal !default;
$btn-box-shadow:              inset 0 1px 0 rgba($white, .15), 0 1px 1px rgba($black, .075) !default;
$btn-focus-width:             $input-btn-focus-width !default;
$btn-focus-box-shadow:        $input-btn-focus-box-shadow !default;
$btn-disabled-opacity:        .65 !default;
$btn-active-box-shadow:       inset 0 3px 5px rgba($black, .125) !default;

$btn-link-disabled-color:     $gray-600 !default;

$btn-block-spacing-y:         .5rem !default;

// Allows for customizing button radius independently from global border radius
$btn-border-radius:           $border-radius !default;
$btn-border-radius-lg:        $border-radius !default;
$btn-border-radius-md:        $border-radius !default;
$btn-border-radius-xs:        $border-radius !default;
$btn-border-radius-xxs:       $border-radius !default;
$btn-border-radius-sm:        $border-radius !default;
$btn-transition:              color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out !default;


$custom-forms-transition:               background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out !default;
